import React from "react";
import VerticalTab from "../components/VerticalTab/VerticalTab";
import { Grid, GridItem, Stack } from "@chakra-ui/react";
import Paragraph from "../components/Paragraph";
import { MdArrowForward } from "react-icons/md";

import { featuresParagraphs } from "../data/data";

function OurFeatures () {
  return (
    <Grid my={10} p={8} templateColumns='repeat(18, 1fr)'>
      <GridItem colSpan={{base: 18, xl: 2}}>
        <VerticalTab text="Компетенции" />
      </GridItem>

      <GridItem colSpan={{base: 18, xl: 16}}>
        <Stack spacing={16}>
          {
            featuresParagraphs.map((paragraph) => (
              <Paragraph title={paragraph.title} text={paragraph.text} marker markerIcon={MdArrowForward} />
            ))
          }
        </Stack>
      </GridItem>
    </Grid>
  )
}

export default OurFeatures