import React from "react";
import VerticalTab from "../components/VerticalTab/VerticalTab";
import { Grid, GridItem, Stack } from "@chakra-ui/react";
import Location from "../components/Location";

import Contact from "../components/Contact";
import { contacts } from "../data/data";

function Contacts () {
  return (
    <Grid my={10} p={8} templateColumns='repeat(18, 1fr)'>
      <GridItem colSpan={{base: 18, xl: 2}}>
        <VerticalTab text="Контакты" />
      </GridItem>

      <GridItem colSpan={{base: 18, xl: 8}} pr={{base: 0, xl: 20}}>
        <Stack spacing={{base: 10, xl: 20}}>
          {
            contacts.people.map((contact) => (
              <Contact
              fio={contact.fio}
              phone={contact.phone}
              email={contact.email}
            />
            ))            
          }
        </Stack>
      </GridItem>

      <GridItem colSpan={{base: 18, xl: 8}} pt={{base: 10, xl: 0}}>
        <Location />
      </GridItem>
    </Grid>
  )
}

export default Contacts